'use client';

import { useState } from 'react';
import dynamic from 'next/dynamic';
import BakeryList from '../../../components/BakeryList';
import { bakeries, Bakery } from '../../../data/bakeries';

// Dynamically import the map to avoid SSR issues with Leaflet
const BakeryMap = dynamic(() => import('../../../components/BakeryMap'), {
    ssr: false,
    loading: () => (
        <div className="w-full h-full bg-gray-50 flex flex-col items-center justify-center text-gray-400">
            <svg className="animate-spin h-10 w-10 mb-4 text-pink-500" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
            </svg>
            <span className="text-lg font-medium">Loading Map...</span>
        </div>
    )
});

export default function BakeryFinderPage() {
    const [selectedBakeryId, setSelectedBakeryId] = useState<string | undefined>(undefined);

    const handleBakerySelect = (bakery: Bakery) => {
        setSelectedBakeryId(bakery.id);
    };

    return (
        <div className="flex flex-col h-[calc(100vh-64px)] bg-gray-50">
            <div className="bg-white border-b border-gray-200 px-6 py-4 shadow-sm z-10 flex justify-between items-center">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900 flex items-center gap-2">
                        <span className="text-3xl">üó∫Ô∏è</span> Bakery Finder
                    </h1>
                    <p className="text-gray-500 text-sm mt-1">Discover the best bakeries across the US. Curated by our experts.</p>
                </div>
                <div className="hidden md:block">
                    <span className="bg-pink-100 text-pink-800 text-xs font-bold px-3 py-1 rounded-full uppercase tracking-wide">
                        {bakeries.length} Locations
                    </span>
                </div>
            </div>

            <div className="flex-1 flex flex-col md:flex-row overflow-hidden relative">
                {/* List View */}
                <div className="w-full md:w-1/3 lg:w-1/4 h-1/2 md:h-full z-20 md:z-auto shadow-xl md:shadow-none order-2 md:order-1 border-r border-gray-200 bg-white">
                    <BakeryList
                        bakeries={bakeries}
                        onSelect={handleBakerySelect}
                        selectedId={selectedBakeryId}
                    />
                </div>

                {/* Map View */}
                <div className="w-full md:w-2/3 lg:w-3/4 h-1/2 md:h-full order-1 md:order-2 relative bg-gray-100">
                    <BakeryMap
                        bakeries={bakeries}
                        selectedBakeryId={selectedBakeryId}
                        onBakerySelect={handleBakerySelect}
                    />
                </div>
            </div>
        </div>
    );
}
