'use client';

import { useState } from 'react';
import { Bakery } from '../data/bakeries';

interface BakeryListProps {
    bakeries: Bakery[];
    onSelect: (bakery: Bakery) => void;
    selectedId?: string;
}

export default function BakeryList({ bakeries, onSelect, selectedId }: BakeryListProps) {
    const [searchTerm, setSearchTerm] = useState('');
    const [cityFilter, setCityFilter] = useState('All');

    const cities = ['All', ...Array.from(new Set(bakeries.map(b => b.city))).sort()];

    const filteredBakeries = bakeries.filter(b => {
        const matchesSearch = b.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
            b.specialties.some(s => s.toLowerCase().includes(searchTerm.toLowerCase()));
        const matchesCity = cityFilter === 'All' || b.city === cityFilter;
        return matchesSearch && matchesCity;
    });

    return (
        <div className="flex flex-col h-full bg-white shadow-lg rounded-lg overflow-hidden border border-gray-200">
            <div className="p-4 border-b bg-pink-50">
                <h2 className="text-xl font-bold text-gray-800 mb-4">Find a Bakery</h2>
                <div className="space-y-3">
                    <input
                        type="text"
                        placeholder="Search bakeries or specialties..."
                        className="w-full p-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-pink-500 focus:border-transparent outline-none"
                        value={searchTerm}
                        onChange={(e) => setSearchTerm(e.target.value)}
                    />
                    <select
                        className="w-full p-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-pink-500 outline-none"
                        value={cityFilter}
                        onChange={(e) => setCityFilter(e.target.value)}
                    >
                        {cities.map(city => (
                            <option key={city} value={city}>{city}</option>
                        ))}
                    </select>
                </div>
            </div>

            <div className="flex-1 overflow-y-auto p-4 space-y-4">
                {filteredBakeries.map(bakery => (
                    <div
                        key={bakery.id}
                        onClick={() => onSelect(bakery)}
                        className={`p-4 border rounded-lg cursor-pointer transition-all hover:shadow-md ${selectedId === bakery.id ? 'border-pink-500 bg-pink-50 ring-1 ring-pink-500' : 'border-gray-200 hover:border-pink-300'}`}
                    >
                        <div className="flex justify-between items-start">
                            <div>
                                <h3 className="font-bold text-lg text-gray-900">{bakery.name}</h3>
                                <p className="text-sm text-gray-600">{bakery.address}</p>
                                <p className="text-xs text-gray-500">{bakery.city}, {bakery.state}</p>
                            </div>
                            <span className="bg-green-100 text-green-800 text-xs font-bold px-2 py-1 rounded flex items-center">
                                {bakery.rating} ‚òÖ
                            </span>
                        </div>

                        <p className="text-sm text-gray-700 mt-2 line-clamp-2">{bakery.description}</p>

                        <div className="mt-3 flex flex-wrap gap-1">
                            {bakery.specialties.slice(0, 3).map(s => (
                                <span key={s} className="text-xs bg-gray-100 text-gray-600 px-2 py-0.5 rounded-full border border-gray-200">
                                    {s}
                                </span>
                            ))}
                        </div>

                        <div className="mt-3 flex justify-between items-center text-sm border-t pt-2">
                            <span className="text-gray-500 font-medium">{bakery.priceRange} ‚Ä¢ {bakery.reviewCount.toLocaleString()} reviews</span>
                            <button className="text-pink-600 font-medium hover:underline text-xs uppercase tracking-wide">
                                View on Map
                            </button>
                        </div>
                    </div>
                ))}

                {filteredBakeries.length === 0 && (
                    <div className="text-center py-10 text-gray-500">
                        <p className="text-lg font-medium">No bakeries found</p>
                        <p className="text-sm">Try adjusting your search or filters</p>
                    </div>
                )}
            </div>
        </div>
    );
}
